import { createWebHistory, createRouter } from "vue-router";
import ListList from "./components/ListList.vue"
import HomeHome from "./components/HomeHome.vue"
import DetailDetail from "./components/DetailDetail.vue"
import AuthorAuthor from "./components/AuthorAuthor.vue"
import CommentComment from "./components/CommentComment.vue"

const routes = [
  {
    path: "/list",
    component: ListList,
  },
  {
    path: "/",
    component: HomeHome,
  },
  {
    path: "/detail/:id",
    component: DetailDetail,
    children: [
      { path: 'author', component: AuthorAuthor },
      { path: 'comment', component: CommentComment },
    ]
  },
  {
    path: "/:anything(.*)",
    component: <p>없는페이지입니다.</p>,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
